import React from 'react'
import Link from 'gatsby-link'
import District from '../../components/District'

const CanGio = ({ data }) => {
  return <District nodes={data.allLocationsXlsxSheet1.edges} />
}

export default CanGio

export const CanGioQuery = graphql`
  query CanGioQuery {
    allLocationsXlsxSheet1(filter: { district: { eq: "Cần Giờ" } }) {
      edges {
        node {
          plf_id
          complete_address
          longitude
          latitude
          city
          ward
          district
          planned_launch_date
          week
          status
          contact_email
          notes
        }
      }
    }
  }
`



